@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins;
}

:root {
  --clr1a: #0084ff;
  --clr1b: #00b7ff;
}

::selection {
  background-color: var(--clr1a);
  color: #fff;
}

.btn1 {
  padding: 5px 12px;
  background-color: var(--clr1a);
  border: none;
  outline: none;
  color: #fff;
  border-radius: 2.5px;
}

.btn1:hover {
  background-color: var(--clr1b);
}

.navbar {
  width: 100%;
  height: 50px;
  background-color: #fff;
  box-shadow: 0 5px 20px #0001;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
}

.navbar .logo {
  height: 100%;
  margin: 0 2rem;
  padding: 0.75rem 0;
  display: flex;
  align-items: center;
}

.navbar .logo img {
  height: 70%;
}

.navbar .menu {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.navbar .menu li {
  position: relative;
  color: #000;
  margin: 0 1rem;
  font-size: 0.75rem;
  cursor: default;
  transition: 0.5s;
  color: #000;
}

.navbar .menu li:before {
  position: absolute;
  content: "";
  bottom: -2px;
  left: 0;
  width: 15px;
  height: 2px;
  background-color: var(--clr1a);
  transition: 0.5s;
}

.navbar .menu li:hover:before {
  width: 100%;
}
